
#resumePreview {
  background: #fff;
  box-shadow: 0 1px 3px 0 rgba(0,0,0,0.25);
  line-height: 1.2;
  overflow-y: scroll;
}
#resumePreview * {
  box-sizing: border-box;
  font-variant: normal;
  font-weight: normal;
}
#resumePreview ol {
  list-style: none;
}
#resumePreview p {
  white-space: pre-line;
}
#resumePreview #information {
  padding: 2em;
}
#resumePreview #information:after {
  display: block;
  content: '';
  clear: both;
}
#resumePreview #information td {
  width: 5em;
  font-size: 20px;
}
#resumePreview #information section[data-name="profile"] {
  float: left;
}
#resumePreview #information div {
  float: right;
}
#resumePreview #experiences {
  padding: 2em;
}
#resumePreview #experiences section {
  margin-bottom: 2em;
}
#resumePreview #experiences section >h2:first-child {
  display: inline-block;
  margin-bottom: 0.5em;
  color: #000;
  background-color: #d3d3d3;
  padding: 10px;
}
#resumePreview #experiences section[data-name="workHistory"] li+li,
#resumePreview #experiences section[data-name="projects"] li+li,
#resumePreview #experiences section[data-name="awards"] li+li {
  margin-top: 1em;
}
#resumePreview #experiences section[data-name="workHistory"] li h3,
#resumePreview #experiences section[data-name="projects"] li h3,
#resumePreview #experiences section[data-name="awards"] li h3 {
  border-bottom: 1px solid #999;
  padding-bottom: 0.3em;
  margin-bottom: 0.3em;
}
#resumePreview #experiences section[data-name="workHistory"] li h3 p,
#resumePreview #experiences section[data-name="projects"] li h3 p,
#resumePreview #experiences section[data-name="awards"] li h3 p {
  font-size: 14px;
/*color: gray*/
  float: right;
}


